<script>
    import Menu from './components/Menu';
    import ItemAggridTable from "./components/ItemAggridTable";
    import {mapGetters, mapMutations} from 'vuex';

    export default {
        name: 'App',
        components: {
            ItemAggridTable,
            Menu
        },

        data: () => ({}),
        beforeDestroy() {
            if (typeof window !== 'undefined') {
                window.removeEventListener('resize', this.onResize, {passive: true})
            }
        }, beforeMount() {

            this.onResize();
            window.addEventListener('resize', this.onResize, {passive: true})
        },
        computed: {
            ...mapGetters['getResolution', 'isMobile'],
            ...mapMutations['setIsMobile'],
            isMobile(){
              return this.$store.getters.isMobile;
            }
        },
        methods: {

            /**
             * Обработчик события изменения размера окна бразуера
             */
            onResize() {

                //Считаем, что если длинна браузера меньше чем 600, тогда это мобилка
                this.$store.commit('setIsMobile', window.innerWidth < 600);

                //Достаём их стора длинну ширину и подсчитываем
                let storeResolution = this.$store.getters.getResolution;
                let width = window.innerWidth * 0.98;
                let height = (window.innerHeight > storeResolution.height) ? window.innerHeight * 0.90 : storeResolution.height;

                this.$store.commit('setResolution', {"width": width, "height": height})

            }
        },

    };
</script>

