<template>
    <div class="ag-grid-wrapper">
        <ag-grid-vue :style="{width: this.$store.getters.getWidth+'px', height: this.$store.getters.getHeight+'px'}"
                     class="ag-theme-balham"
                     :gridOptions="gridOptions"
                     :columnDefs="columnDefs"
                     :rowData="rowData">
        </ag-grid-vue>
    </div>
</template>

<script>
    import {AgGridVue} from "ag-grid-vue";
    import {mapGetters} from 'vuex';

    export default {
        name: "ItemAggridTable",
        components: {AgGridVue},
        data() {
            return {
                gridOptions: null,
                gridApi: null,
                columnApi: null,
                columnDefs: null,
                defaultColDef: null,
                rowData: null,
                profileId: null,
                width: 1000,
                height: 500
            }
        },

        beforeMount() {

            this.gridOptions = {};
            this.columnDefs = [
                {headerName: 'Номер договора', field: 'dogNum'},
                {headerName: 'Заказчик', field: 'customer'},
                {headerName: 'Вид работ', field: 'workType'},
                {headerName: 'Классификация оборудования', field: 'rigClass'},
                {headerName: 'Завод', field: 'factory'}
            ];

            this.rowData = [];

        },
        mounted() {
            this.gridApi = this.gridOptions.api;
            this.gridColumnApi = this.gridOptions.columnApi;
        },
        computed:{
            ...mapGetters['getWidth','getHeight'],
        }

    }
</script>

<style scoped>
    .ag-grid-wrapper {
        margin: 10px;
    }
</style>
<style lang="scss" scoped>
    @import "../../node_modules/ag-grid-community/dist/styles/ag-grid.css";
    @import "../../node_modules/ag-grid-community/dist/styles/ag-theme-balham.css";
