<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div class="text-center">
        <v-menu offset-y v-for="(button, index0) in buttons" :key="index0" class="menu">

            <template v-slot:activator="{ on:menu }">
                <v-tooltip bottom>
                    <template v-slot:activator="{ on:tooltip }">
                        <v-btn color="grey"
                               v-on="{...tooltip, ...menu }"
                               small
                               style="margin-right: 5px;"
                               :disabled="button.isDisabled"

                        >
                            <v-icon v-if="button.icon" left>{{button.icon}}</v-icon>
                            <template v-if="button.title">
                                <span class="hidden-md-and-down">{{button.title}}</span>
                            </template>

                        </v-btn>
                    </template>
                    <span>{{button.title}}</span>
                </v-tooltip>
            </template>
            <template v-if="button.menus.length">
                <v-list>
                    <v-list-item
                            v-for="(menu, index) in button.menus"
                            :key="index"
                            @click=""
                    >
                        <v-list-item-title>
                            <v-icon v-if="menu.icon">menu.icon</v-icon>
                            {{ menu.title }}
                        </v-list-item-title>
                    </v-list-item>
                </v-list>
            </template>
        </v-menu>


    </div>
</template>

<script>
    export default {
        name: "Menu",
        data: () => ({
            buttons: [
                {
                    title: 'Редактировать',
                    icon: 'mdi-pencil',
                    isDisabled: false,
                    menus: [
                        {title: 'Создать новую запись', icon: ''},
                        {title: 'Задать новые значения', icon: ''}
                    ]
                },
                {
                    title: 'Журналы',
                    icon: 'chrome_reader_mode',
                    isDisabled: true,
                    menus: []
                },
                {
                    title: 'Сеансы',
                    icon: 'note_add',
                    isDisabled: false,
                    menus: [
                        {title: 'Создать новую запись', icon: ''},
                        {title: 'Задать новые значения', icon: ''}
                    ]
                },
                {
                    title: 'Управление персоналом',
                    icon: 'perm_identity',
                    isDisabled: false,
                    menus: []
                },
                {
                    title: 'Статистика и отчёты',
                    icon: 'timeline',
                    isDisabled: false,
                    menus: []
                },
                {
                    title: 'Чат',
                    icon: 'question_answer',
                    isDisabled: false,
                    menus: []
                },
            ],

        }),
    }
</script>

<style scoped>
    .menu {
        margin: 5px;
    }
</style>