import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        device: {
            isMobile: false,
            width: 1000,
            height: 500
        }

    },
    mutations: {
        setIsMobile(state, value) {
            state.device.isMobile = value;
        },
        setResolution(state, resolution) {
            console.log('setResolution',resolution);
            state.device.width = resolution.width;
            state.device.height = resolution.height;
        }
    },
    getters: {
        /**
         * Является ли устройсво мобильным
         * @param state
         * @returns {*}
         */
        isMobile: state => {
            return state.device.isMobile;
        },
        getResolution: state => {
            return {"width": state.device.width, "height": state.device.height}
        },
        getWidth: state =>{
            return state.device.width;
        },
        getHeight: state =>{
            return state.device.height;
        }
    }
})